import Fuse from 'fuse.js'

const BASE_URL = `https://xemovie.co`;
const CORS_URL = `${process.env.REACT_APP_CORS_PROXY_URL}${BASE_URL}`;
let phpsessid;

async function findContent(searchTerm, type) {
    try {
        const searchPage = await fetch(`${CORS_URL}/search?q=${type === "movie" ? encodeURIComponent(searchTerm) : encodeURIComponent(searchTerm)}`).then(r => r.text());
    
        // Parse DOM to find search results on full search page
        const parser = new DOMParser();
        const doc = parser.parseFromString(searchPage, "text/html");
        const nodes = Array.from(doc.getElementById(`${type === 'show' ? '"serie"' : '"movie"'}`).parentElement.parentElement.querySelectorAll("div.grid.grid-cols-2.md\\:grid-cols-3.lg\\:grid-cols-4.gap-3.flow-root.mt-5 > div"));
        const results = nodes.map(node => {
            return {
                type,
                title: node.querySelector('a.text-white.text-base.font-semibold').innerText.trim(),
                year: node.querySelector('.float-right.text-neutral-dark.text-sm.font-normal').innerText.trim(),
                slug: node.querySelector('a').href.split('/').pop(),
            }
        });
    
        const fuse = new Fuse(results, { threshold: 0.3, distance: 200, keys: ["title"] });
        const matchedResults = fuse
            .search(searchTerm.toString())
            .map((result) => result.item);
    
        if (matchedResults.length === 0) {
            return { options: [] }
        }
    
        if (matchedResults.length > 1) {
            const res = { options: [] };
    
            matchedResults.forEach((r) => res.options.push({
                title: r.title,
                slug: r.slug,
                type: r.type,
                year: r.year,
                source: 'xemovie'
            }));
    
            return res;
        } else {
            const { title, slug, type, year } = matchedResults[0];
    
            return {
                options: [{ title, slug, type, year, source: 'xemovie' }]
            }
        }
    } catch (e) {
        return { options: [] }
    }
}
async function getVideoUrl(config) {
    let url = '';

    if (config.type === 'movie') {
        url = `${CORS_URL}/movies/${config.id}/watch`;
    } else if (config.type === 'show') {
        url = `${CORS_URL}/tv-shows/${config.id}/watch`;
    }
    
    const res = await fetch(url).then(d => d.text());

    const scripts = Array.from(new DOMParser().parseFromString(res, "text/html").querySelectorAll('script'));
    const prop = scripts.find((e) => e.textContent.includes("s3.xemovie.com"));

    let data = '';

    if (prop) {
        data = prop.textContent.replace(/\s+/g, '').split(',').find((e) => e.includes("s3.xemovie.com")).split('"').find((e) => e.includes('s3.xemovie.com'))
        if (data.endsWith(".m3u8")) data = "";
        return { videoUrl: data };
    }

    return { url: '',  }
}

async function getEpisodes(slug) {
    const url = `${CORS_URL}/tv-shows/${slug}`;
    const pageReq = await fetch(url, {
        headers: { phpsessid },
    }).then((d) => d.text());

    const parser = new DOMParser();
        const doc = parser.parseFromString(pageReq, "text/html");
        const nodes = Array.from(doc.querySelectorAll("div.grid.grid-cols-3.gap-3 > a"));
        const results = nodes.map(node => {
            return {
                title: node.innerText.trim(),
                episode: node.innerText.split(' ').pop().trim(),
                id: node.href.split('/').slice(-2).reverse().pop(),
                season: "1",
            }
        });
    
        const data = new Fuse(results, { threshold: 0.3, distance: 200, keys: ["title"] });

    let seasons = [];
    let episodes = [];
    data._docs.forEach((e) => {
        if (!seasons.includes(e.season))
            seasons.push(e.season);

        if (!episodes[e.season])
            episodes[e.season] = []
        episodes[e.season].push(e.episode)
    })

    return { seasons, episodes }
}

async function getStreamUrl(slug, type, season, episode) {
    let id = '';

    if (type === "movie") {
        id = slug;
    }

    if (type === "show") {
        const url = `${CORS_URL}/${type === 'show' ? 'tv-shows' : 'movies'}/${slug}`;
        const pageRes = await fetch(url);
        if (pageRes.headers.get('phpsessid')) phpsessid = pageRes.headers.get('phpsessid');
        const pageResText = await pageRes.text();

        // Parse DOM to find search results on full search page
        const parser = new DOMParser();
        const doc = parser.parseFromString(pageResText, "text/html");
        const nodes = Array.from(doc.querySelectorAll("div.grid.grid-cols-3.gap-3 > a"));
        const results = nodes.map(node => {
            return {
                title: node.innerText.trim(),
                episode: node.innerText.split(' ').pop().trim(),
                id: node.href.split('/').slice(-2).reverse().pop(),
                season: "1",
            }
        });
    
        const data = new Fuse(results, { threshold: 0.3, distance: 200, keys: ["title"] });

        const episodeObj = data._docs.find((v) => { return v.season === season && v.episode === episode; });

        if (episodeObj) {
            id = episodeObj.id;
        }
    }

    if (id === '') {
        return { url: '' }
    }

    const videoUrl = await getVideoUrl({
        slug: slug,
        id: id,
        type: type,
    }); 

    return { url: videoUrl.videoUrl };
}


const xemovie = { findContent, getStreamUrl, getEpisodes };
export default xemovie;
