import Fuse from 'fuse.js'

const BASE_URL = `https://akwam.io`;
const CORS_URL = `${process.env.REACT_APP_CORS_PROXY_URL}${BASE_URL}`;
let phpsessid;

async function findContent(searchTerm, type) {
    try {
        const searchUrl = `${CORS_URL}/search?q=${encodeURIComponent(searchTerm)}`;
        const searchRes = await fetch(searchUrl).then((d) => d.text());
    
        // Parse DOM to find search results on full search page
        const parser = new DOMParser();
        const doc = parser.parseFromString(searchRes, "text/html");
        const nodes = Array.from(doc.querySelectorAll('.col-lg-auto.col-md-4.col-6.mb-12'));
        const results = nodes.map(node => {
            return {
                type,
                title: node.querySelector('a.text-white').innerText.trim(),
                year: node.querySelector('span.badge.badge-pill.badge-secondary.ml-1').innerText.trim(),
                slug: node.querySelector('a').href.split('/').slice(-2).reverse().pop()+"/"+node.querySelector('a').href.split('/').pop(),
            }
        });
    
        const fuse = new Fuse(results, { threshold: 0.3, distance: 200, keys: ["title"] });
        const matchedResults = fuse
            .search(searchTerm.toString())
            .map((result) => result.item);
    
        if (matchedResults.length === 0) {
            return { options: [] }
        }
    
        if (matchedResults.length > 1) {
            const res = { options: [] };
    
            matchedResults.forEach((r) => res.options.push({
                title: r.title,
                slug: r.slug,
                type: r.type,
                year: r.year,
                source: 'akwam'
            }));
    
            return res;
        } else {
            const { title, slug, type, year } = matchedResults[0];
    
            return {
                options: [{ title, slug, type, year, source: 'akwam' }]
            }
        }
    } catch (e) {
        return { options: [] }
    }
}
async function getVideoUrl(config) {
    let surl = '';

    if (config.type === 'movie') {
        surl = `${CORS_URL}/movie/${config.id}`;
    } else if (config.type === 'show') {
        surl = `${CORS_URL}/episode/${config.id}`;
    }
    
    const pageReq = await fetch(surl, {
        headers: { phpsessid },
    }).then((d) => d.text());

    const parser = new DOMParser();
        const doc = parser.parseFromString(pageReq, "text/html");
        const node = Array.from(doc.querySelectorAll('#tab-4'))[0].querySelectorAll('a.link-btn.link-show.d-flex.align-items-center.px-3')[0].href.split('/').pop();

    let url = '';

    if (config.type === 'movie') {
        url = `${CORS_URL}/download/${node}/${config.id}`;
    } else if (config.type === 'show') {
        url = `${CORS_URL}/download/${node}/${config.id}`;
    }

    const data = await fetch(url, {
        headers: { phpsessid },
    }).then((d) => d.text());

    const videoDoc = parser.parseFromString(data, "text/html");
    const videoUrl = Array.from(videoDoc.querySelectorAll('a.font-size-16.text-muted'))[0].href;    

    return {
        videoUrl: videoUrl,
    };
}

async function getEpisodes(slug) {
    const url = `${CORS_URL}/series/${slug}`;
    const pageReq = await fetch(url, {
        headers: { phpsessid },
    }).then((d) => d.text());

    const parser = new DOMParser();
        const doc = parser.parseFromString(pageReq, "text/html");
        const nodes = Array.from(doc.querySelectorAll('.bg-primary2.p-4.col-lg-4.col-md-6.col-12'));
        const results = nodes.map(node => {
            return {
                title: node.querySelector('img.img-fluid').alt.split(' : ')[1].trim(),
                episode: node.querySelector('img.img-fluid').alt.split(' : ')[0].trim(),
                id: node.querySelector('a').href.split('/').slice(-2).reverse().pop()+"/"+node.querySelector('a').href.split('/').pop(),
                season: "1",
            }
        });
    
        const data = new Fuse(results, { threshold: 0.3, distance: 200, keys: ["title"] });

    let seasons = [];
    let episodes = [];
    data._docs.forEach((e) => {
        if (!seasons.includes(e.season))
            seasons.push(e.season);

        if (!episodes[e.season])
            episodes[e.season] = []
        episodes[e.season].push(e.episode)
    })

    return { seasons, episodes }
}

async function getStreamUrl(slug, type, season, episode) {
    let id = '';

    if (type === "movie") {
        id = slug;
    }

    if (type === "show") {
        const url = `${CORS_URL}/${type === 'show' ? 'series' : type}/${slug}`;
        const pageRes = await fetch(url);
        if (pageRes.headers.get('phpsessid')) phpsessid = pageRes.headers.get('phpsessid');
        const pageResText = await pageRes.text();

        // Parse DOM to find search results on full search page
        const parser = new DOMParser();
        const doc = parser.parseFromString(pageResText, "text/html");
        const nodes = Array.from(doc.querySelectorAll('.bg-primary2.p-4.col-lg-4.col-md-6.col-12'));
        const results = nodes.map(node => {
            return {
                title: node.querySelector('img.img-fluid').alt.split(' : ')[1].trim(),
                episode: node.querySelector('img.img-fluid').alt.split(' : ')[0].trim(),
                id: node.querySelector('a').href.split('/').slice(-2).reverse().pop()+"/"+node.querySelector('a').href.split('/').pop(),
                season: "1",
            }
        });
    
        const data = new Fuse(results, { threshold: 0.3, distance: 200, keys: ["title"] });

        const episodeObj = data._docs.find((v) => { return v.season === season && v.episode === episode; });

        if (episodeObj) {
            id = episodeObj.id;
        }
    }
    
    if (id === '') {
        return { url: '' }
    }

    const videoUrl = await getVideoUrl({
        slug: slug,
        id: id,
        type: type,
    }); 

    return { url: videoUrl.videoUrl };
}


const akwam = { findContent, getStreamUrl, getEpisodes };
export default akwam;
