import theflix from './scraper/theflix';
import trailersto from './scraper/trailersto';
import xemovie from './scraper/xemovie';
import akwam from './scraper/akwam';

async function findContent(searchTerm, type) {
    const results = { options: []};
    const content = await Promise.all([
        trailersto.findContent(searchTerm, type),
        theflix.findContent(searchTerm, type),
        xemovie.findContent(searchTerm, type),
        akwam.findContent(searchTerm, type),
    ]);

    content.forEach((o) => {
        if (!o || !o.options) return;

        o.options.forEach((i) => {
            if (!i) return;
            results.options.push(i)
        })
    });

    return results;
}

async function getStreamUrl(slug, type, source, season, episode) {
    switch (source) { 
        case 'trailersto':
            return await trailersto.getStreamUrl(slug, type, season, episode);        
        case 'theflix':
            return await theflix.getStreamUrl(slug, type, season, episode);
        case 'xemovie':
            return await xemovie.getStreamUrl(slug, type, season, episode);
        case 'akwam':
            return await akwam.getStreamUrl(slug, type, season, episode);
        default:
            return;
    }
}

async function getEpisodes(slug, source) {
    switch (source) {
        case 'trailersto':
            return await trailersto.getEpisodes(slug);
        case 'theflix':
            return await theflix.getEpisodes(slug);
        case 'xemovie':
            return await xemovie.getEpisodes(slug);
        case 'akwam':
            return await akwam.getEpisodes(slug);
        default:
            return;
    }
}

export { findContent, getStreamUrl, getEpisodes }